import React, { useState } from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import Container from "react-bootstrap/Container";
import { Link } from "react-router-dom";
import { AiOutlineHome, AiOutlineFundProjectionScreen, AiOutlineUser,} from "react-icons/ai";
import {animateScroll as scroll} from 'react-scroll';
import { CgFileDocument } from "react-icons/cg";

function NavBar() {
  const [expand, updateExpanded] = useState(false);
  const [navColour, updateNavbar] = useState(false);

  function scrollHandler() {
    if (window.scrollY >= 20) {
      updateNavbar(true);
    } else {
      updateNavbar(false);
    }
  }
  const toggleHome = () => { scroll.scrollToTop(); };   ; const toggleAbout = () => { scroll.scrollTo(1570); }; const toggleProjects = () => { scroll.scrollTo(3450); };const toggleResume = () => { scroll.scrollTo(5050)
  }
  window.addEventListener("scroll", scrollHandler);

  return (
    <Navbar
      expanded={expand}
      fixed="top"
      expand="md"
      className={navColour ? "sticky" : "navbar"}
    >
      <Container>
  
        <Navbar.Toggle
          aria-controls="responsive-navbar-nav"
          onClick={() => {
            updateExpanded(expand ? false : "expanded");
          }}
        >
          <span></span>
          <span></span>
          <span></span>
        </Navbar.Toggle>
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto" defaultActiveKey="#home">
            <Nav.Item><Nav.Link as={Link} to="/" onClick={toggleHome}><AiOutlineHome style={{ marginBottom: "2px" }} /> Home </Nav.Link></Nav.Item>

            <Nav.Item> <Nav.Link onClick={toggleAbout}>  <AiOutlineUser style={{ marginBottom: "2px" }} /> About </Nav.Link> </Nav.Item>

            <Nav.Item> <Nav.Link onClick={toggleProjects}> <AiOutlineFundProjectionScreen style={{ marginBottom: "2px" }} />{" "}  Projects </Nav.Link> </Nav.Item>

            <Nav.Item> <Nav.Link onClick={toggleResume}> <CgFileDocument style={{ marginBottom: "2px" }} /> Resume </Nav.Link></Nav.Item>
        
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavBar;